.hero {
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
	flex-wrap: wrap;
	padding: 0 35px 130px;
	box-sizing: border-box;
	min-height: 100vh;
	height: auto;
	width: 100%;
	box-sizing: border-box;
	@include image('../img/hero-bg.jpg');
	@include cover;
	@include r(1500) {
		padding: 0 30px 100px; }
	@include r(1400) {
		padding: 0 30px 80px; }
	@include r(1300) {
		padding: 0 25px 60px; }
	@include r(1200) {
		padding: 0 20px 40px; }
	@include r(992) {
		padding: 0 15px 20px; }
	&-logo {
		display: flex;
		justify-content: center;
		align-items: center;
		box-sizing: border-box;
		height: 100%;
		width: 75%;
		@include r(992) {
			margin: auto;
			width: 100%;
			height: 300px; }
		@include r(460) {
			padding-right: 50px; }
		@include r(460) {
			padding-right: 80px; }
		&__logo {
			max-width: 100%;
			max-height: 100%;
			margin-top: -10%;
			width: 35%;
			height: auto;
			@include r(992) {
				width: 200px; } } }

	&-info {
		display: flex;
		align-items: center;
		width: 55%;
		@include r(1850) {
			width: 65%; }
		@include r(1600) {
			width: 70%; }
		@include r(1260) {
			flex-basis: calc(100% - 370px); }
		@include r(992) {
			flex-basis: 100%;
			width: 100%; }
		&__big-year {
			padding-left: 20px;
			box-sizing: border-box;
			@include fs(125);
			line-height: 0;
			font-weight: 200;
			@include r(1440) {
				@include fs(150); }
			@include r(1260) {
				display: none; } }
		&-content {
			padding-right: 12px;
			box-sizing: border-box;
			border-right: 1px solid transparentize($light, .35);
			@include r(992) {
				margin-top: auto;
				margin-bottom: 40px;
				width: 100%; }
			&__text {
				box-sizing: border-box;
				margin-bottom: 5px;
				padding-bottom: 5px;
				@include fs(18);
				@include lh(18, 20);
				border-bottom: 1px solid transparentize($light, .79);
				@include r(992) {
					margin-bottom: 10px;
					padding-bottom: 10px; } }
			&-years {
				display: flex;
				justify-content: space-between;
				@include r(992) {
					align-items: center;
					flex-wrap: wrap; }
				@include r(600) {
					justify-content: flex-start; }
				&__year {
					margin: 0 0 5px;
					width: 10%;
					@include fs(18);
					@include r(600) {
						width: 15%; }
					@include r(400) {
						width: 20%; }
					@include r(360) {
						width: 30%; } } } } } }


