}

.lines {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  box-sizing: border-box;
  width: 43px;
  height: 3px;
  background-color: transparent;
  border: 1px solid #afafaf;
  transition: .25s;
}

.lines::before, .lines::after {
  content: '';
  position: absolute;
  right: -1px;
  box-sizing: border-box;
  width: 43px;
  height: 3px;
  background-color: transparent;
  border: 1px solid #afafaf;
  transition: .25s;
}

.lines::before {
  top: -8px;
}

.lines::after {
  bottom: -8px;
}

.is-active .lines {
  border-color: transparent;
}

.is-active .lines::before {
  top: -1px;
  transform: rotate(45deg);
}

.is-active .lines::after {
  bottom: -1px;
  transform: rotate(-45deg);
}

